.navigation__list {
  list-style: none;
  padding: 20px;
  padding-top: 15px;
  margin: 0;
  border-radius: 10px;
}

.navigation__list-item {
  margin-top: 5px;
}

.navigation__navlink {
  &.active{
    text-decoration: underline;
  }
  &:hover {
    @include font-astra(18px);
  }
}
